import { HEL_AUDIO_TYPE_VOICE, SKYWAY_API_HOST, worldViewerWindow } from './const';
import { debugLog } from './debugLog';
import { getLocalStream, getMixerSettingAt, getPeer, getSkyRoomId, getSkywayKey, getSkywayName, getSkywayOnScreenShareStarted, getSkywayPlayerList, setLocalStream, setPeer, setSkyRoomId, setSkywayAvatarNumber, setSkywayAvatarNumberForSend, setSkywayName, setSkywayPlayerList, setSkywayRoom } from './logics/dataOps';
import { requestSendingReceivedPacketsFromSkyway } from './logics/moduleCaller';
/**
 * heliodor_skyway_rcpから参照があるためglobal関数
 * @param peerId
 */ worldViewerWindow.hel_skyway_create_player = (peerId)=>{
    const skywayPlayer = new CHELSkywayPlayer(peerId, getMixerSettingAt(HEL_AUDIO_TYPE_VOICE));
    const skywayPlayerList = getSkywayPlayerList();
    //	debugLog("hel_skyway_create_player " + peerId);
    for(let n = 0; n < skywayPlayerList.length; n++){
        if (skywayPlayerList[n] === null) {
            setSkywayPlayerList(skywayPlayer, n);
            return skywayPlayer;
        }
    }
    skywayPlayerList.push(skywayPlayer);
    return skywayPlayer;
};
/**
 * heliodor_skyway_xrw, heliodor_skyway_rcpから参照があるためglobal関数
 * @param peerId
 */ worldViewerWindow.hel_skyway_release_player = (peerId)=>{
    const skywayPlayerList = getSkywayPlayerList();
    for(let n = 0; n < skywayPlayerList.length; n++){
        const skywayPlayer = skywayPlayerList[n];
        if (skywayPlayer === null) {
            continue;
        }
        if (skywayPlayer.getPeerId() === peerId) {
            skywayPlayer.release();
            setSkywayPlayerList(null, n);
            return;
        }
    }
};
/**
 * heliodor_skyway_rcp, heliodor_skyway_xrw, heliodor_xrw_mainから参照があるためglobal関数
 * @param peerId
 */ worldViewerWindow.hel_skyway_get_player = (peerId)=>{
    const skywayPlayerList = getSkywayPlayerList();
    for(let n = 0; n < skywayPlayerList.length; n++){
        const skywayPlayer = skywayPlayerList[n];
        if (skywayPlayer == null) {
            continue;
        }
        if (skywayPlayer.getPeerId() === peerId) {
            return skywayPlayer;
        }
    }
    return null;
};
/**
 * heliodor_skyway_rcp, heliodor_skyway_xrwから参照があるためglobal関数
 * @param stream
 */ worldViewerWindow.hel_skyway_stream = (stream)=>{
    if (!stream.peerId) {
        debugLog('stream.peerId not');
        return;
    }
    let player = worldViewerWindow.hel_skyway_get_player(stream.peerId);
    if (player == null) {
        player = worldViewerWindow.hel_skyway_create_player(stream.peerId);
        if (player == null) {
            debugLog('hel_skyway_get_player not ' + stream.peerId);
            return;
        }
    }
    player.setStream(stream);
};
/**
 * heliodor_skyway_rcp, heliodor_skyway_xrwから参照があるためglobal関数
 * @param volume
 */ worldViewerWindow.hel_skyway_SetMixerVolume = (volume)=>{
    const skywayPlayerList = getSkywayPlayerList();
    for(let n = 0; n < skywayPlayerList.length; n++){
        const skywayPlayer = skywayPlayerList[n];
        if (skywayPlayer == null) {
            continue;
        }
        skywayPlayer.setVolume(volume);
    }
};
