/**
 * heliodor_skyway_rcp, heliodor_skyway_xrw, heliodor_xrw_mainから参照があるためglobal関数
 * TODO
 * agora/skyway/photon/chalcedonyで今後realtimeEngineSDKに分岐
 * @virtual
 */ worldViewerWindow.hel_skyway_join = ()=>{
    //	const leaveTrigger = document.getElementById('js-leave-trigger');
    const remoteVideos = document.getElementById('js-remote-streams');
    // Note that you need to ensure the peer has connected to signaling server
    // before using methods of peer instance.
    const peer = getPeer();
    const localStream = getLocalStream();
    if (peer === undefined || localStream === undefined) {
        debugLog('worldViewerWindow.g_peer or worldViewerWindow.g_localStream is undefined');
        return;
    }
    if (!peer.open) {
        debugLog('hel_skyway_join : worldViewerWindow.g_peer not open');
        return;
    }
    //
    const parts = location.hostname.split('.');
    const subdomain = parts.shift();
    const skywayRoomId = subdomain + '_' + getSkyRoomId();
    //
    const room = peer.joinRoom(skywayRoomId, {
        mode: 'mesh',
        stream: localStream
    });
    setSkywayRoom(room);
    room.once('open', ()=>{
        //		worldViewerWindow.hel_skyway_add_text_chat("", 'You joined');
        worldViewerWindow.hel_skyway_receive_data('open', peer.id, '');
        // to set my name
        worldViewerWindow.hel_skyway_receive_data('data', peer.id, 'name:' + getSkywayName());
        // send my name to old player
        worldViewerWindow.hel_skyway_send_myinfo();
    });
    room.on('peerJoin', (peerId)=>{
        worldViewerWindow.hel_skyway_create_player(peerId);
        //		worldViewerWindow.hel_skyway_add_text_chat("", `${peerId} joined`);
        //		worldViewerWindow.hel_skyway_receive_data("join", peerId, "");
        // send my name to new player
        worldViewerWindow.hel_skyway_send_myinfo();
    });
    // Render remote stream for new peer join in the room
    room.on('stream', async (stream)=>{
        worldViewerWindow.hel_skyway_stream(stream);
        const newAudio = document.createElement('audio');
        newAudio.srcObject = stream;
        newAudio.playsInline = true;
        // mark peerId to find it later at peerLeave event
        newAudio.setAttribute('data-peer-id', stream.peerId);
        remoteVideos.append(newAudio);
        await newAudio.play().catch(console.error);
    });
    room.on('data', ({ data , src  })=>{
        // Show a message sent to the room and who sent
        //      messages.textContent += `${src}: ${data}\n`;
        //debugLog(`${src}: ${data}`);
        if (data === 'screenshare:shared') {
            const fn = getSkywayOnScreenShareStarted();
            if (fn !== undefined) {
                fn();
            }
        }
        worldViewerWindow.hel_skyway_receive_data('data', src, data);
    });
    // for closing room members
    room.on('peerLeave', (peerId)=>{
        worldViewerWindow.hel_skyway_release_player(peerId);
        const remoteVideo = remoteVideos.querySelector(`[data-peer-id="${peerId}"]`);
        remoteVideo.srcObject.getTracks().forEach((track)=>track.stop()
        );
        remoteVideo.srcObject = null;
        remoteVideo.remove();
        //      messages.textContent += `=== ${peerId} left ===\n`;
        //		worldViewerWindow.hel_skyway_add_text_chat("", `${peerId} leaved`);
        worldViewerWindow.hel_skyway_receive_data('leave', peerId, '');
    });
    // for closing myself
    room.once('close', ()=>{
        //      messages.textContent += '== You left ===\n';
        Array.from(remoteVideos.children).forEach((remoteVideo)=>{
            remoteVideo.srcObject.getTracks().forEach((track)=>track.stop()
            );
            remoteVideo.srcObject = null;
            remoteVideo.remove();
        });
    });
};
/**
 * @remarks
 * 呼出元でmediaDevicesがundefinedでないことを確認
 */ const setLocalAudioStreamBy = async ()=>{
    const localStream = await navigator.mediaDevices.getUserMedia({
        audio: true,
        video: false
    }).catch(console.error);
    setLocalStream(localStream);
    return localStream;
};
/**
 * @remarks
 * Peerの初期化
 * @remarks
 * Peerの初期化を分離
 * @param configKey
 * @returns Peer|undefined
 */ worldViewerWindow.initializePeer = async (configKey)=>{
    const localVideo = document.getElementById('js-local-stream');
    // const remoteVideos = document.getElementById('js-remote-streams') as HTMLVideoElement;
    //
    // const sdkSrc = document.querySelector('script[src*=skyway]');
    if (!navigator.mediaDevices) {
        debugLog('navigator.mediaDevices not found');
        return undefined;
    }
    const localStream = await setLocalAudioStreamBy();
    // default mic mute
    worldViewerWindow.hel_skyway_set_mic_state(false);
    // Render local stream
    localVideo.muted = true;
    localVideo.srcObject = localStream;
    localVideo.playsInline = true;
    await localVideo.play().catch(console.error);
    const peer = new Peer({
        key: configKey === undefined ? getSkywayKey() : configKey,
        debug: 0
    });
    setPeer(peer);
    peer.on('error', console.error);
    return getPeer();
};
/**
 * @remarks
 * Peer経由でジョインする設定を実施
 */ worldViewerWindow.joinViaPeer = ()=>{
    const peer = getPeer();
    if (peer === undefined) {
        throw new Error('peer has not been set yet');
    }
    peer.on('open', ()=>{
        worldViewerWindow.hel_skyway_join();
        worldViewerWindow.hel_skyway_display_room_video();
    });
};
