worldViewerWindow.hel_skyway_main_with_display_media = async ()=>{
    const localVideo = document.getElementById('js-local-stream');
    // const remoteVideos = document.getElementById('js-remote-streams') as HTMLVideoElement;
    //
    // const sdkSrc = document.querySelector('script[src*=skyway]');
    if (!navigator.mediaDevices) {
        debugLog('navigator.mediaDevices not found');
        return;
    }
    const localAudioStream = await setLocalAudioStreamBy();
    const localVideoStream = await navigator.mediaDevices.getDisplayMedia({
        audio: false,
        video: {
            width: {
                ideal: 1280
            },
            height: {
                ideal: 720
            },
            frameRate: {
                ideal: 30
            }
        }
    }).catch(console.error);
    localAudioStream.addTrack(localVideoStream.getVideoTracks()[0]);
    // default mic mute
    worldViewerWindow.hel_skyway_set_mic_state(false);
    // Render local stream
    localVideo.muted = true;
    localVideo.srcObject = localAudioStream;
    localVideo.playsInline = true;
    await localVideo.play().catch(console.error);
    // eslint-disable-next-line require-atomic-updates
    worldViewerWindow.g_peer = new Peer({
        key: worldViewerWindow.g_SKYWAY_KEY,
        debug: 0
    });
    worldViewerWindow.g_peer.on('error', console.error);
    worldViewerWindow.g_peer.on('open', ()=>{
        worldViewerWindow.hel_skyway_join();
        worldViewerWindow.hel_skyway_display_room_video();
    });
};
worldViewerWindow.hel_skyway_start_screen_sharing = async ()=>{
    if (!navigator.mediaDevices) {
        debugLog('navigator.mediaDevices not found');
        return;
    }
    const localVideoStream = await navigator.mediaDevices.getDisplayMedia({
        audio: true,
        video: {
            width: {
                ideal: 1280
            },
            height: {
                ideal: 720
            },
            frameRate: {
                ideal: 30
            }
        }
    }).catch(console.error);
    const localStream = worldViewerWindow.g_localStream;
    localVideoStream.getVideoTracks()[0].onended = function() {
        localStream.removeTrack(localVideoStream.getVideoTracks()[0]);
    };
    localStream.addTrack(localVideoStream.getVideoTracks()[0]);
    worldViewerWindow.hel_skyway_send_data('screenshare:shared');
    const room = worldViewerWindow.g_room;
    room.close();
    worldViewerWindow.hel_skyway_join();
    worldViewerWindow.g_Video.srcObject = localStream;
    worldViewerWindow.g_Video.playsInline = true;
};
worldViewerWindow.hel_skyway_display_room_video = ()=>{
    if (worldViewerWindow.g_room != null) {
        // const room = worldViewerWindow.g_room;
        // room.close();
        // worldViewerWindow.hel_skyway_join();
        worldViewerWindow.g_room.on('stream', (stream)=>{
            if (worldViewerWindow.g_Video != null) {
                if (worldViewerWindow.g_Video.srcObject !== stream) {
                    worldViewerWindow.g_Video.srcObject = stream;
                }
                worldViewerWindow.g_Video.playsInline = true;
            }
        });
    }
};
